import React, { useState } from "react";
import '../PaymentStyle.scss';
import { useLocation } from 'react-router-dom';

export default function PaymentPage() {
    const [ccNumber, setCcNumber] = useState(""); //    
    const [valueDate, setValueDate] = useState<number | ''>(''); //       
    const [valueCode, setValueCode] = useState<number | ''>(''); //    
    const location = useLocation(); //    URL
    const queryParams = new URLSearchParams(location.search);
    const price = queryParams.get('price'); //    URL
    
    const formatAndSetCcNumber = (e: React.ChangeEvent<HTMLInputElement>) => { //    
        const inputVal = e.target.value.replace(/ /g, ""); //     
        let inputNumbersOnly = inputVal.replace(/\D/g, ""); //   ,  

        if (inputNumbersOnly.length > 16) { //     16 
            inputNumbersOnly = inputNumbersOnly.substr(0, 16); //    16 
        }

        const splits = inputNumbersOnly.match(/.{1,4}/g); //       4 

        let spacedNumber = ""; //       
        if (splits) { //    
            spacedNumber = splits.join(" "); //      4 
        }

        setCcNumber(spacedNumber); //     
    };

    const handleChangeDate = (event: React.ChangeEvent<HTMLInputElement>) => { //       
        const inputValue = event.target.value; //   
        if (inputValue.length <= 4) { //       4 
            setValueDate(inputValue === '' ? '' : Number(inputValue)); //    
        }
    };

    const handleChangeCode = (event: React.ChangeEvent<HTMLInputElement>) => { //    
        const inputValue = event.target.value; //   
        if (inputValue.length <= 3) { //       3 
            setValueCode(inputValue === '' ? '' : Number(inputValue)); //    
        }
    };
    
    return(
        <section className="payment-page">
            <h2 className="payment-page__price">{price}  </h2>
            <div className="payment-page__payment-card">
                <h3 className="payment-card__heading">   </h3>
                <input className="payment-card__input" type="text" placeholder="" value={ccNumber} onChange={formatAndSetCcNumber}/>
                <div className="payment-card__inputs-group">
                    <input className="payment-card__input" type="number" placeholder="/" value={valueDate} onChange={handleChangeDate}/>
                    <input className="payment-card__input" type="number" placeholder="CVC/CVV" value={valueCode} onChange={handleChangeCode}/>
                </div>
            </div>
            <a href="scam" className="payment-page__pay-link">  </a>
        </section>
    )
}