import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import Cookies from 'js-cookie';

interface LoginMenuProps { //     LoginMenu
  toggleLoginMenu: () => void; //      
}

export default function LoginMenu({ toggleLoginMenu }: LoginMenuProps) {
  const [isLoginMode, setIsLoginMode] = useState(true); //       
  const [login, setLogin] = useState(''); //     
  const [password, setPassword] = useState(''); //     
  const navigate = useNavigate(); //   

  const toggleMode = () => setIsLoginMode(!isLoginMode); //       

  const handleClose = () => { //     
    document.body.classList.remove('no-scroll'); //   "no-scroll"   
    toggleLoginMenu(); //      
  };

  useEffect(() => { //     "no-scroll"      
    document.body.classList.add('no-scroll');
    return () => {
      document.body.classList.remove('no-scroll');
    };
  }, []);

  const handleAuth = async (isRegistering: boolean) => { //    
    const baseUrl = window.location.origin; //    
    
    try {
      let response;
      if (isRegistering) {
        response = await axios.get(
          `${baseUrl}/api/post/user?login=${encodeURIComponent(login)}&password=${encodeURIComponent(password)}`
        );
      } else {
        response = await axios.get(
          `${baseUrl}/api/get/user?login=${encodeURIComponent(login)}&password=${encodeURIComponent(password)}`
        );
        if (response.data.user.length === 0) {
          alert('  .');
          return;
        }
      }

      if (response.status === 200) {
        Cookies.set('user', login, { expires: 1 }); //    
        Cookies.set('user_id', response.data.user[0].id, { expires: 1 }); //    ID 
        navigate('/profile'); //    
        toggleLoginMenu(); //      
      }
    } catch (error) {
      alert('  : ' + error);
    }
  };

  const handleSubmit = async (event: React.FormEvent<HTMLFormElement>) => { //     
    event.preventDefault();
    await handleAuth(!isLoginMode);
  };

  return (
    <>
      <div className="background-blackout" onClick={handleClose}></div>
      <form className="popup-login" onSubmit={handleSubmit}>
        <div className="popup-login__top-container">
          <div className="top-container__headings-text">
            <h5 className="popup-menu__heading">
              SusMarket <span>ID</span>
            </h5>
            <p className="top-container__text">
              {isLoginMode ? '  SusMarket ID' : '  SusMarket ID'}
            </p>
          </div>
        </div>
        <div className="popup-login__inputs-container">
          <input
            type="text"
            name="userName"
            id="userName"
            className="popup-login__name-input"
            placeholder=""
            value={login}
            onChange={(e) => setLogin(e.target.value)}
          />
          <input
            type="password"
            name="userPassword"
            id="userPassword"
            className="popup-login__password-input"
            placeholder=""
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
        </div>
        <div className="popup-login__bottom-container">
          <p className="popup-login__prompt-url" onClick={toggleMode}>
            {isLoginMode ? '   ? ' : '   ? '}
            <u>{isLoginMode ? '' : ''}</u>
          </p>
          <motion.button
            type="submit"
            className="popup-login__login-button"
            whileTap={{ scale: 0.98 }}
            transition={{ duration: 0.2, type: 'spring' }}
          >
            {isLoginMode ? '' : ''}
          </motion.button>
        </div>
      </form>
    </>
  );
}